import React from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ListGroup from 'react-bootstrap/ListGroup'
import BorderlessListGroupItemWithBlueRadioButton from '../components/BorderlessListGroupItemWithBlueRadioButton'
import BlueArrowRight from '../components/BlueArrowRight'
import { Link, useLocation } from 'react-router-dom'

const RegisterMainPage = () => {
  const location = useLocation()
  return (
    <>
      <Row>
        <Col md={4}></Col>
        <Col md={'auto'}>
          <h1
            style={{ borderStyle: 'solid', borderColor: 'blue' }}
            className='pr-5 pl-5'
          >
            New User
          </h1>
        </Col>
      </Row>
      <Row className='mt-5'>Are you?</Row>
      <ListGroup>
        <BorderlessListGroupItemWithBlueRadioButton href='#person'>
          Person
        </BorderlessListGroupItemWithBlueRadioButton>
        <BorderlessListGroupItemWithBlueRadioButton href='#institution-needs-halls'>
          Institution needs halls
        </BorderlessListGroupItemWithBlueRadioButton>
        <BorderlessListGroupItemWithBlueRadioButton href='#institution-has-halls'>
          Institution has halls
        </BorderlessListGroupItemWithBlueRadioButton>
      </ListGroup>
      <Row className='mt-3'>
        <Col md={10}></Col>
        <Col>
          <Link
            to={`${location.pathname}/${location.hash.substring(
              1,
              location.hash.length
            )}`}
          >
            <BlueArrowRight />
          </Link>
        </Col>
      </Row>
    </>
  )
}

export default RegisterMainPage
