import React from 'react'
import RegisterPersonPage from './RegisterPersonPage'
import RegisterInstitutionNeedsHallsPage from './RegisterInstitutionNeedsHallsPage'
import RegisterInstitutionHasHallsPage from './RegisterInstitutionHasHallsPage'
import RegisterMainPage from './RegisterMainPage'
import { useParams } from 'react-router-dom'
const RegisterPage = () => {
  let { registerType } = useParams()
  switch (registerType) {
    case 'person':
      return <RegisterPersonPage />
    case 'institution-needs-halls':
      return <RegisterInstitutionNeedsHallsPage />
    case 'institution-has-halls':
      return <RegisterInstitutionHasHallsPage />
    default:
      return <RegisterMainPage />
  }
}

export default RegisterPage
