import React from 'react'
import Masa7atiButton from '../components/Masa7atiButton'
import LanguageButton from '../components/LanguageButton'
import Image from 'react-bootstrap/Image'
import { Link } from 'react-router-dom'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import ControlledCarousel from '../components/ControlledCarousel'

const HomePage = () => {
  return (
    <>
      <Row className='mt-5'>
        <Col md={4}>
          <Image
            src='logo.png'
            rounded
            style={{ width: '200px', height: '50px' }}
          />
        </Col>
        <Col md={6}>
          <h1>Masa7ati | مساحتي</h1>
        </Col>
        <Col md={1}>
          <div class='btn-toolbar'>
            <Link to='/login'>
              <Masa7atiButton className='btn btn-primary mr-2 mb-2'>
                Login
              </Masa7atiButton>
            </Link>
            <Link to='/register'>
              <Masa7atiButton className='btn btn-primary mr-2 mb-2'>
                Register
              </Masa7atiButton>
            </Link>
          </div>
        </Col>
        <Col md={1} className='mt-4'>
          <LanguageButton variant='outline-dark'>Arabic</LanguageButton>
        </Col>
      </Row>
      <Row>
        <Col>
          <Navbar bg='light' variant='light'>
            <Nav className='mr-auto'>
              <Nav.Link href='#home'>Who are we?</Nav.Link>
            </Nav>
            <Nav>
              <Nav.Link href='#features'>What we do?</Nav.Link>
            </Nav>
            <Nav className='ml-auto'>
              <Nav.Link href='#pricing'>Connect us</Nav.Link>
            </Nav>
          </Navbar>
        </Col>
      </Row>
      <Row>
        <Col>
          <ControlledCarousel />
        </Col>
      </Row>
    </>
  )
}

export default HomePage
